import Vue from 'vue';
import Router from 'vue-router';
const Home = () => import('@/page/home');
const Login = () => import('@/page/user/Login');

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Home',
            component: Home
        },
        {
            path: '/login',
            name: 'Login',
            component: Login
        }
    ]
});



